import React, { Component } from 'react';


import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';



import '../../../public/css/App.css';
import AssessmentComponent from './AssessmentComponent';
import ChooseLevelComponent from './chooseLevelComponent';
import AssessmentResult from './AssessmentResult';

import * as actionCreators from '../../redux/actionCeators'

class AssessmentContainer extends Component {

   constructor() {
    super();
    this.state = {
      answer:'',
      answerBoxClass:'wordEntryBoxDefault well',
      formState:'waitingforanswer'
    };
    this.handleAnswer = this.handleAnswer.bind(this);
    this.submitAnswer = this.submitAnswer.bind(this);
    this.getNextWord = this.getNextWord.bind(this);
  }


handleAnswer(e){

  this.setState({answer: e.target.value})

//KEEP THIS STUFF - IT'S FOR HANDLING ANSWERS DYNAMICALLY
//   this.setState({answerBoxClass:'wordEntryBoxCorrect'})

//   const questionArray = [...this.props.word]
//   const answerArray = [...e.target.value]

//   for(var i=0; i< answerArray.length; i++){
    
//     if(questionArray[i] !== answerArray[i]){
//       this.setState({answerBoxClass:'wordEntryBoxWrong'})
//       break;
//     }
//     else if ((i + 1) === questionArray.length){ 

//       //To Do: record sucess and failure 
//       this.setState({formState:'answercorrect'})
  
//     return true
//   }
// }
}

submitAnswer(e){
  e.preventDefault();

  const cleanedAnswer = this.state.answer.trim().toLowerCase()
  this.props.submitAssessmentAnswer(this.props.assessmentId, 
                                    this.props.questionId, 
                                    cleanedAnswer)
  
  this.setState({answer : ''})

  };

getNextWord(){
  console.log('get Next Word')
  this.props.getNextWordAction(this.props.challengeId)
  this.setState({ answerBoxClass:'wordEntryBoxDefault well',
                  formState:'waitingforanswer'})
}

  render() {

    switch(this.props.assessmentState){
      case 'end of test':
        return <AssessmentResult {...this.props} />
      case 'in progress':
        return <AssessmentComponent style={this.state.answerBoxClass}
                        word={this.props.word}
                        remaining={this.props.remaining}
                        compareWords={this.handleAnswer}
                        submitAnswer={this.submitAnswer}
                        formState={this.state.formState}
                        getNextWord={this.getNextWord}
                        value={this.state.answer}
                        />
    default:
      return <ChooseLevelComponent {...this.props} />
    ; 
    }
  }
}

function mapStateToProps(state){
  
    return {
      assessmentState: state.assessment.assessmentState,
      assessmentId : state.assessment.assessmentId,
      questionId: state.assessment.questionId,
      startingLevel : state.assessment.startingLevel,
      word: state.assessment.word,
      remaining: state.assessment.remaining,
      results: state.results
    }
}


function mapDispatchToProps(dispatch){
    return bindActionCreators(actionCreators, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(AssessmentContainer)





