//make api call to initialise a challenge

function getAssessmentInit(action){
    console.log('call api with ' + JSON.stringify(action))

// const request = new Request('http://localhost:8080/api/newAssessment', {
const request = new Request('/api/newAssessment', {
      method: 'POST',
      headers: new Headers({
        'Content-Type': 'application/json'
      }), 
      body: (JSON.stringify({assessmentLevel : action.assessmentLevel}))
    });

      return fetch(request)
          .then(response => {
              // console.log(response.json())
            return response.json();
          })
}


export default getAssessmentInit


