import { call, put , takeEvery } from 'redux-saga/effects';
import getAssessmentInit from '../data/assessment'
import submitAnswer from '../data/answer'

//1 Workers

export function* callGetAssesment(action){
    try{
        // console.log('callGetChallenge - calling API ' + action.assessmentLevel)

        const response = yield call(getAssessmentInit, action)
        
        // console.log('Saga got this ' + JSON.stringify(response))
        yield put({type: 'GOT_ASSESSMENT_INIT', data: response})
    }
    catch(e){console.log('API ERROR' + e)}
}

export function* callSubmitAnswer(action){
    try{
        // console.log('SAGA - calling api')
        
        const response = yield call(submitAnswer, action)

        yield put({type: 'GOT_ANSWER_RESULT', result: response})
    }
    catch(e){console.log('SAGA - api call error ' + e)}
}

//2. Watchers

export function* watchChallengeInitiationCall(){
    yield takeEvery('INIT_ASSESSMENT', callGetAssesment)
};

export function* watchForAnswerSubmission(){
    yield takeEvery('SUBMIT_ASSESSMENT_ANSWER', callSubmitAnswer)
}


//3. Root Saga

export default function* saga(){

yield [
  watchChallengeInitiationCall(),
  watchForAnswerSubmission()
]
}


