import React from 'react';
import PropTypes from 'prop-types';
import ReactAudioPlayer from 'react-audio-player'


const AssessmentComponent = props => {
    return (
        <div>
        <div className="jumbotron">
            <div>You have {props.remaining} words to go!</div>
        
        {props.formState === 'waitingforanswer' ?
            <div>
                <p>"Can you spell {props.word}"</p>
                <div>
                <ReactAudioPlayer
                    src="/audiofiles/Brag.mp3"
                    autoPlay
                />
                </div>
                <form onSubmit={(e)=>{props.submitAnswer(e)}}>
               <input   id='entryBox'
                        className={props.style} 
                        type='text' 
                        onChange={(e)=>{props.compareWords(e)}}
                        value={props.value}
                        autoFocus
                        />
                <button type='submit' className='btn btn-danger'>Done</button>
                </form>
                
            </div>
            :
            <div><h2>Awesome you spelled {props.word} !</h2></div>
        }     
                
           {props.formState === 'answercorrect' && 
           <button className="btn btn-success"
                    onClick={(e)=>props.getNextWord()}
           >Next Question</button>}

        </div>
            
        </div>
    );
};

AssessmentComponent.propTypes = {
    word: PropTypes.string.isRequired,
    compareWords : PropTypes.func.isRequired,
    style: PropTypes.string.isRequired,
    formState : PropTypes.string.isRequired,
    getNextWord: PropTypes.func.isRequired
    
};

export default AssessmentComponent;


