function assessment(state=[], action){
// console.log('CHALLENGE REDUCER')
switch(action.type){

    case 'GOT_ASSESSMENT_INIT':
    // console.log('GOT_ASSESSMENT_INIT reducer got this ' + JSON.stringify(action.data))
    return{
        assessmentId: action.data.assessmentId,
        questionId: action.data.questionId,
        word: action.data.word,
        remaining: action.data.remaining,
        assessmentState : action.data.status
    }
    case 'GOT_ANSWER_RESULT':
    console.log('REDUCER - GOT_ANSWER_RESULT = ' + JSON.stringify(action.result.questionId))
    return {
        assessmentId: action.result.assessmentId,
        questionId: action.result.questionId,
        word: action.result.word,
        remaining: action.result.remaining,
        assessmentState : action.result.status
    }

    case 'GOT_NEXT_WORD':
    console.log('Reducer GOT_NEXT_WORD ' + JSON.stringify(action))
     return{
        assessmentId: action.data.assessmentId,
        // assessment: action.data.assessment,
        word: action.data.word,
        questionId: action.data.questionId,
        assessmentState : 'in progress',
        remaining: action.data.remaining
    }

    default:
        return state;
        }
};

export default assessment;


