import React from 'react';
import PropTypes from 'prop-types';

const ChooseLevelComponent = props => {
    return (
        <div className="jumbotron">
            <div onClick={()=>props.initialiseAssessment(1)}> I'm ages between 5 and 15</div>
            <div onClick={()=>props.initialiseAssessment(2)}> I'm older than 15</div>
        </div>
    );
};

ChooseLevelComponent.propTypes = {
    initialiseAssessment : PropTypes.func.isRequired
    
};

export default ChooseLevelComponent;


