import React, { Component } from 'react';
import { Link } from 'react-router';

class HomeContainer extends Component {
    render() {
        return (
            
                <div className="container">
            <h1>This is the homepage</h1>
            <div className="jumbotron"><h2>Welcome to Pro Spelling</h2>
            <h3>On <b>{process.env.NODE_ENV}</b></h3>
            <p>{process.env.REACT_APP_DATABASE_URL}</p>
            <p>We've got a really sophisticated app that uses Kerry's technique and some clever technology to teach you how to spell!</p>
            <p>First we have to work out what level you are at</p>
            <Link to="/assessment">Click here and we'll find out</Link>
            </div>
        </div> 
            
        );
    }
}

export default HomeContainer;


