

export function initialiseAssessment(assessmentLevel){
    // console.log('ACTION CREATOR - initialiseAssessment')
    return {
        type: 'INIT_ASSESSMENT',
        assessmentLevel : assessmentLevel
    }
}

export function submitAssessmentAnswer(assessmentId, questionId, answer){
    console.log('ACTION_CREATOR ' + questionId + ' ' +answer )
    return{
        type: 'SUBMIT_ASSESSMENT_ANSWER',
        assessmentId: assessmentId,
        questionId: questionId,
        answer: answer
    }
}


