function results(state=[], action){

switch(action.type){
    case 'GOT_ANSWER_RESULT':
    console.log('REDUCER - GOT_ANSWER_RESULT = ' + JSON.stringify(action))
    return [...state,
        {questionId: action.result.questionId,
        word: action.result.word,
        answer: action.result.answer,
        answerTimeStamp: action.result.answerTimeStamp}
    ]
    
    default:
        return state
    }
}

export default results;


